export class Questionary {
  id: number;
  name: string;
  plan: Plan;
  questions: Question[];
  answered: boolean;
  icon: string;
}

export class Answer {
  plan: Plan;
  questionary: Questionary;
  question: Question;
  respondent: Respondent;
  answer: number;
  created_at: string;
  isCompleted: boolean;
  type: string;
}

export class AnswerNeighborhood {
  plan: Plan;
  questionary: Questionary;
  question: Question;
  respondent: Respondent;
  neighborhood: Neighborhood;
  created_at: string;
}



