import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { RestProvider } from "../rest/rest";
import { Respondent } from "../../pages/respondent-profile/respondent-profile";

@Injectable()
export class RespondentProvider {

  constructor(public http: HttpClient, public restProvider: RestProvider) {
  }

  getRespondentByCPF(cpf) {
    return new Promise(resolve => {
      let data = {
        "cpf": cpf
      };
      this.http.post(this.restProvider.API_URL + "get-respondent-by-cpf.php", data, { headers: this.restProvider.headers })
        .subscribe(data => {
          resolve(data);
        }, error => {
          resolve(error);
        });
    });
  }

  updateRespondent(respondent: Respondent) {
    return new Promise(resolve => {
      let updatedAt = new Date().toISOString();
      let token = this.restProvider.cryptography(respondent.id + respondent.cpf + respondent.email + respondent.residenceTimeRange + respondent.residenceNeighborhood.id + respondent.salaryRange + updatedAt);
      let data = {
        "id": respondent.id,
        "cpf": respondent.cpf,
        "email": respondent.email,
        "residence_time_range": respondent.residenceTimeRange,
        "residence_neighborhood_id": respondent.residenceNeighborhood.id,
        "job_city_id": respondent.jobCity.id,
        "job_neighborhood_id": respondent.jobNeighborhood.id,
        "salary_range": respondent.salaryRange,
        "updated_at": updatedAt,
        "token": token,
        //-----------------NOVOS CAMPOS-----------------
        "age_range": respondent.ageRange,
        "gender": respondent.gender,
        "name": respondent.name,
        "phone": respondent.phone,
        "type": respondent.type
        //-----------------NOVOS CAMPOS-----------------
      };
      //-----------------INSERO API-----------------
      this.http.post(this.restProvider.API_URL + "update-respondent.php", data, { headers: this.restProvider.headers })
        .subscribe(data => {
          //-----------------INSERO API-----------------
          resolve(data);
          //-----------------INSERO API-----------------
        }, error => {
          console.log(error);
          resolve(error);
        });
      //-----------------INSERO API-----------------
    });
  }

  insertRespondent(respondent: Respondent) {
    return new Promise(resolve => {
      let createdAt = new Date().toISOString();
      let token = this.restProvider.cryptography(respondent.cpf + respondent.email + respondent.residenceTimeRange + respondent.residenceNeighborhood.id + respondent.salaryRange + createdAt);
      let data = {
        "cpf": respondent.cpf,
        "email": respondent.email,
        "residence_time_range": respondent.residenceTimeRange,
        "residence_neighborhood_id": respondent.residenceNeighborhood.id,
        "job_city_id": respondent.jobCity.id,
        "job_neighborhood_id": respondent.jobNeighborhood.id,
        "salary_range": respondent.salaryRange,
        "created_at": createdAt,
        //-----------------NOVOS CAMPOS-----------------
        "age_range": respondent.ageRange,
        "gender": respondent.gender,
        "name": respondent.name,
        "phone": respondent.phone,
        "type": respondent.type,
        //-----------------NOVOS CAMPOS-----------------
        "token": token
      };
      //-----------------INSERO API-----------------
      this.http.post(this.restProvider.API_URL + "insert-respondent.php", data, { headers: this.restProvider.headers })
        .subscribe(data => {
          //-----------------INSERO API-----------------
          resolve(data);
          //-----------------INSERO API-----------------
        }, error => {
          resolve(error);
        });
      //-----------------INSERO API-----------------
    });
  }
}



